import React, { useState } from "react";
import { Container, Typography, CardContent, Grid, Box, CardActions, Card, Button as MuiButton, useTheme, useMediaQuery } from "@material-ui/core";
import { Link } from "gatsby";
import Layout from "../components/layout";

const PricingPage = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const StyledCard = {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    boxShadow: 'none',
    border: '1px solid #000',
    height: '100%'
  };

  const DividerBox = {
    height: '2px',
    backgroundColor: '#000',
    margin: theme.spacing(2, 0),
  };

  const pricingOptions = [
    {
      currency: "₹",
      amount: "600",
      period: "Yearly",
      description: "Secure a year's worth of enlightening reads with our monthly magazine.",
    },
    {
      currency: "₹",
      amount: "1000",
      period: "Yearly",
      description: "Elevate your knowledge with our monthly magazine and a riveting book on a unique topic every month for a full year.",
    },
  ];

  const [hoveredButton, setHoveredButton] = useState(null);

  const StyledButton = (variant, index) => ({
    backgroundColor: variant === 'outlined' ? 'transparent' : "#2f6697",
    borderColor: variant === 'outlined' ? '#f44336' : undefined,
    borderWidth: variant === 'outlined' ? '1px' : undefined,
    borderStyle: variant === 'outlined' ? 'solid' : undefined,
    color: variant === 'outlined' ? 'black' : 'white',
    padding: '2px 10px',
    textTransform: 'none',
    transition: 'all .3s ease',
    textDecoration: hoveredButton === index ? 'underline' : 'none',
  });

  return (
    <Layout>
      <Container maxWidth="md">
        <Typography variant="h2" component="h2" gutterBottom align="center">
          Subscription
        </Typography>
        <Typography variant="body1" gutterBottom align="center">
          Our website is an ad-free zone, and with our subscription model, you can help us sustain this. For just 50 rupees a month, you not only support unbiased content, but also receive our unique monthly magazine.
        </Typography>
        <Typography variant="body1" gutterBottom align="center">
          Our magazine stands out from the rest by presenting information in easy-to-understand flow diagrams. Our magazine is dedicated to providing you with clear and concise information on a variety of topics, from current events to historical analysis. Imagine unraveling the complexities of India’s economic transition since independence through a comprehensive flow diagram
        </Typography>
        <div style={{ textAlign: 'center', marginBottom: theme.spacing(4) }}>
  <MuiButton
    component={Link}
    to="/flow-diagram-link"
    color="primary"
    variant="outlined"
    style={StyledButton('outlined', 'discover')}
    onMouseEnter={() => setHoveredButton('discover')}
    onMouseLeave={() => setHoveredButton(null)}
  >
    Discover How
  </MuiButton>
</div>

        <Box marginBottom={8}>
          <Grid container justifyContent="center" style={{ gap: isSmallScreen ? '50px' : '80px' }}>
            {pricingOptions.map((option, index) => (
              <Grid item key={index} xs={12} sm={6} md={3}>
                <Card style={StyledCard}>
                  <CardContent style={{ flex: '1 0 auto' }}>
                    <Box style={DividerBox} />
                    <Typography variant="h5" component="h2" align="center">
                      {option.currency}
                    </Typography>
                    <Typography variant="h4" component="p" align="center" gutterBottom>
                      {option.amount}
                    </Typography>
                    <Typography variant="body1" component="p" align="center" gutterBottom>
                      {option.period}
                    </Typography>
                    <Box style={DividerBox} />
                    <Typography variant="body2" component="p" align="center" marginTop={2}>
                      {option.description}
                    </Typography>
                  </CardContent>
                  <CardActions style={{ justifyContent: 'center' }}>
                    <MuiButton
                      component ={Link}
                      to="https://rzp.io/l/8tmhbgDj6"
                      variant="contained"
                      color="primary"
                      style={StyledButton('contained', index)}
                      onMouseEnter={() => setHoveredButton(index)}
                      onMouseLeave={() => setHoveredButton(null)}
                    >
                      Subscribe
                    </MuiButton>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
        <Typography variant="body1" gutterBottom align="center">
          Even if a subscription isn't your cup of tea right now, you can still contribute to this knowledge sharing platform. Consider supporting us with a donation.
        </Typography>
        <div style={{ textAlign: 'center', marginBottom: theme.spacing(4) }}>
  <MuiButton
    variant="contained"
    color="secondary"
    style={StyledButton('contained', 'contribute')}
    onMouseEnter={() => setHoveredButton('contribute')}
    onMouseLeave={() => setHoveredButton(null)}
  >
    Contribute Here
  </MuiButton>
</div>

      </Container>
    </Layout>
  );
};

export default PricingPage;
